export default {
  name: 'Articles',
  props: ['isSelect', 'changeState', 'symbleto', 'symblefrom', 'isHideType'],
  data () {
    return {
      loading: true,
      popupstate: false,
      serachOrder: {
        pageindex: 1,
        pagesize: 20,
        orderstatus: '0,1'
      },
      searchParam: {},
      orderDetail: [],
      market: [],
      total: 0,
      pageSize: 7,//当前条数
      pageNo: 1,//当前页
      currentCondition: [],//当前委托数据(条件)
      pageCurrentData: [],//当前委托数据
      isType: false,//隐藏其他交易对状态
      timeInterval: null,
      token: '',
      background: '',
    }
  },
  mounted () {
    this.$nextTick(() => {
      // // 注销登录
      // this.$bus.on("user-logout", () => {
      //   this.init();
      // });
      // // 登录成功
      // this.$bus.on("Login-Success", () => {
      //   this.init();
      // });
      // //如果下单了就更新下数据
      this.$bus.on('tradeAddOrade', () => {
        this.init();
      });
      // //监听订单
      // this.$bus.on('HashRateOrderChange', () => {
      //   this.init();
      // });
      // this.$bus.on('changeTheme', () => {
      //   this.background = this.$store.state.background
      // })
      this.init();
    })
  },
  created () {
  },
  destroyed () {
    clearInterval(this.timeInterval)
  },
  methods: {
    init () {
      this.token = this.$store.state.token
      if (this.$store.state.token) {
        this.currentOrder();
      } else {
        this.pageCurrentData = []
        this.total = 0
        this.loading = false;
      }
    },
    //获取当前委托订单
    currentOrder () {
      let obj = {
        pageSize: this.pageSize,//当前条数  
        page: this.pageNo,//当前页
      }
      this.$api.Trade.GetAdvertList(obj).then((res) => {
        if (res.isSuccess) {
          this.pageCurrentData = res.data.rows;
          this.total = res.data.count;
          this.loading = false;
        }
      })
    },
    //撤销当前订单
    revocation (item) {
      let id = `${item.id}`;
      this.$api.HashRate.revocation(id).then((res) => {
        if (res && res.data.code === 200) {
          this.pageNo = 1;
          this.total = 0
          this.currentOrder();
          clearInterval(this.timeInterval)
          this.timeInterval = setTimeout(() => {
            this.$bus.emit('currentOrder')
          }, 1000)
        }
      })
    },
    handChange (val) {
      this.pageNo = val;
      this.init()
    },
    //显示登录
    showLoginModalBox () {
      this.$bus.$emit("BusClick");
    },
    //显示注册
    showRegModalBox () {
      this.$bus.$emit("BusRegClick");
    },
  },
}
